an Wood，Ron Lim一起超过75个问题（以及几个一次拍摄和迷你剧）后，该系列结束，Sandu Florea和Randy Elliott结束了这个系列赛。在她在地狱火俱乐部度过的日子之前，这个系列提供了她过去的一瞥，并介绍了她的姐妹Adrienne和Cordelia。")])]),_c('v-card',{staticClass:"card-img"},[_c('v-img',{attrs:{"width":"180","height":"240","src":""}}),_c('v-card-title',{attrs:{"primary-title":""}},[_c('div',[_c('span')])])],1)],1),_c('br'),_c('h2',[_vm._v("影视作品及游戏")]),_c('hr',{staticClass:"mt-3 v-divider theme--light"})])}
